<template>
  <div v-zinx="{ page: 'limit' }" class="page login">
    <div class="wrapper-mask"></div>
    <div class="login_comp">
      <component :is="loginModalName" :state="true" :is-icon="false" @loginSuccess="loginSuccess" />
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'login',
  computed: {
    ...mapState('base', ['isApp']),
    ...mapState('stepLoader', ['loginModalName'])
  },
  mounted() {
    // 获取手机号支持国家的列表
    this.$store.dispatch('common/getAreaCodesList')
  },
  methods: {
    loginSuccess() {
      const baseLocation = location.origin + location.pathname
      location.href = baseLocation
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/limitation.modules.scss';

// 针对移动端样式
.wrapper-mask {
  background: $login_wrapper_bg_pc !important;
}
.login_comp {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

// 针对pc样式
@include breakpoint-only-up() {
  .login_comp {
    min-width: $login_pc_width;
    top: 50%;
    left: 50%;
    bottom: auto;
    right: auto;
    transform: translate(-50%, -50%);
    border-radius: $login_pc_radius;
    overflow: hidden;
  }
}
</style>
